import React, { Component } from 'react';
// import Particles from 'react-particles-js';
import ParticlesBg from 'particles-bg'
import FaceRecognition from './components/FaceRecognition/FaceRecognition';
import Navigation from './components/Navigation/Navigation';
import Signin from './components/Signin/Signin';
import Register from './components/Register/Register';
import Logo from './components/Logo/Logo';
import ImageLinkForm from './components/ImageLinkForm/ImageLinkForm';
import Rank from './components/Rank/Rank';
import './App.css';
import Modal from './components/Modal/Modal';
import Profile from './components/Profile/Profile';

const baseUrl = 'http://localhost:8000';

const initialState = {
  input: '',
  imageUrl: '',
  box: [],
  route: 'signin',
  isSignedIn: false,
  isProfileOpen: false,
  user: {
    id: '',
    name: '',
    email: '',
    entries: 0,
    joined: '',
    pet: 'Dog',
    age: '30'
  }
}


class App extends Component {
  constructor() {
    super();
    this.state = initialState;
  }

  componentDidMount() {
    const token = window.sessionStorage.getItem('token');
    console.log('before token', token)
    if (token) {
      console.log('here in fetch')
      fetch(`${baseUrl}/signin`, {
        method: 'post',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': token
        }
      })
        .then(resp => resp.json())
        .then(data => {
          console.log('dataaaaa', data)
          if (data && data.id) {
            fetch(`${baseUrl}/profile/${data.id}`, {
              method: 'get',
              headers: {
                'Content-Type': 'application/json',
                'Authorization': token
              }
            })

              .then(resp => resp.json())
              .then(user => {
                console.log("user", user)
                if (user && user.email) {
                  this.loadUser(user)
                  this.onRouteChange('home')
                }
              })
          }
        })
        .catch(error => console.error('Error fetching data:', error))
    }
  }

  loadUser = (data) => {
    console.log("loadUser", data)
    this.setState({
      user: {
        id: data.id,
        name: data.name,
        email: data.email,
        entries: data.entries,
        joined: data.joined,
        pet: data.pet,
        age: data.age
      }
    })
  }

  calculateFaceLocation = (data) => {
    if (data && data.outputs) {
      let clarifaiMultipleFaces = data.outputs[0].data.regions
      const image = document.getElementById('inputimage');
      const width = Number(image.width);
      const height = Number(image.height);
      let elements = [];
      clarifaiMultipleFaces.forEach(element => {
        elements.push({
          leftCol: element.region_info.bounding_box.left_col * width,
          topRow: element.region_info.bounding_box.top_row * height,
          rightCol: width - (element.region_info.bounding_box.right_col * width),
          bottomRow: height - (element.region_info.bounding_box.bottom_row * height)
        })

      });

      return elements;
    }
  }

  displayFaceBox = (box) => {
    if (box) {
      this.setState({ box: box });
    }

  }

  onInputChange = (event) => {
    this.setState({ input: event.target.value });
  }

  onButtonSubmit = () => {
    this.setState({ imageUrl: this.state.input });
    fetch(`${baseUrl}/imageurl`, {
      method: 'post',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': window.sessionStorage.getItem("token")
      },
      body: JSON.stringify({
        input: this.state.input
      })
    })
      .then(response => response.json())
      .then(response => {
        if (response) {
          fetch(`${baseUrl}/image`, {
            method: 'put',
            headers: {
              'Content-Type': 'application/json',
              'Authorization': window.sessionStorage.getItem("token")
            },
            body: JSON.stringify({
              id: this.state.user.id
            })
          })
            .then(response => response.json())
            .then(count => {
              this.setState(Object.assign(this.state.user, { entries: count }))
            })
            .catch(console.log)

        }
        this.displayFaceBox(this.calculateFaceLocation(response))
      })
      .catch(err => console.log(err));
  }

  onRouteChange = (route) => {
    if (route === 'signout') {
      window.sessionStorage.removeItem('token')
      return this.setState(initialState)
    } else if (route === 'home') {
      this.setState({ isSignedIn: true })
    }
    this.setState({ route: route });
  }

  toggleModal = () => {
    this.setState(prevState => ({
      ...prevState, isProfileOpen: !prevState.isProfileOpen
    })

    )
  }



  render() {
    const { isSignedIn, imageUrl, route, box, isProfileOpen, user } = this.state;
    return (
      <div className="App">
        <ParticlesBg type="circle" bg={true} />
        <Navigation isSignedIn={isSignedIn} onRouteChange={this.onRouteChange} user={this.state.user} toggleModal={this.toggleModal} />
        {isProfileOpen &&
          <Modal >
            <Profile isProfileOpen={isProfileOpen} toggleModal={this.toggleModal} user={user} loadUser={this.loadUser} />
            {'hello'}
          </Modal>}
        {route === 'home'
          ? <div>
            <Logo />
            <Rank
              name={this.state.user.name}
              entries={this.state.user.entries}
            />
            <ImageLinkForm
              onInputChange={this.onInputChange}
              onButtonSubmit={this.onButtonSubmit}
            />
            <FaceRecognition box={box} imageUrl={imageUrl} />
          </div>
          : (
            route === 'signin'
              ? <Signin loadUser={this.loadUser} onRouteChange={this.onRouteChange} />
              : <Register loadUser={this.loadUser} onRouteChange={this.onRouteChange} />
          )
        }
      </div>
    );
  }
}

export default App;
